@import url('https://fonts.googleapis.com/css?family=Bangers');

.App {
  text-align: center;
}

.App-header {
  padding: 20px;
  color: white;
  text-align: left;
  background-image: -webkit-gradient(linear,left top, right top,from(#C6021C) ,to(#D20233));
  background-image: -webkit-linear-gradient(left,#C6021C ,#D20233);
  background-image: -o-linear-gradient(left,#C6021C ,#D20233);
  background-image: linear-gradient(to right,#C6021C ,#D20233);
  height: 100px;
  display: inline-block;
  width: 100%;
}
.App-title{
  font-size: 3.5em;
  font-family: 'Bangers', cursive;
}
a{
  text-decoration: none;
  font-weight: bold;
}
.Form{
    margin-top: 50px;
}

.Button{
  font-size:150%;
  background-color: #222;
  color:white;
  border-radius: 2px;
  padding:10px 10px;
  border: 0;
}

.Input{
  font-size:150%;
  color: white;
  background-color:#222;
  border:0;
  border-radius: 5px;
  padding: 10px 10px;
}



